import './styles/App.css';
import React, {useState} from 'react';
import Clock from "./components/CurrentDate";
import TodoHeader from "./components/TodoHeader";
import InputForm from "./components/InputForm";
import TodoList from "./components/TodoList";
import axios from "axios";


const App = () => {
  const [inputState, setInputState] = useState('');
  const [todoState, setTodoState] = useState([
    // {id: 0, text: 'Todo list test', checked: false},
  ]);

  const handleInputChange = (e) => {
    const value = e.target.value
    setInputState(value)
  }

  const handleInputCreate = () => {
    setInputState('')
    setTodoState(
      todoState.concat(
        {
          id: Math.random(),
          text: inputState,
          checked: false,
          done: false,
        }
      )
    )
  }

  const handleKeyPress = (e) => {
    //   Enter  handleCreate 
    if (e.key === 'Enter') {
      handleInputCreate();
    }
  }

  const handleToggle = (id) => {
    //   id     .
    const index = todoState.findIndex(todo => todo.id === id);
    const selected = todoState[index]; //  
    const nextTodos = [...todoState]; //  

    //   , checked  
    nextTodos[index] = {
      ...selected,
      checked: !selected.checked
    };

    setTodoState(nextTodos);
  }

  const handleInputRemove = (id) => {
    //  id      
    setTodoState(
      todoState.filter(todo => todo.id !== id)
    );
  }
  
  const getData = async () => {
    try {
      // 
      const response = await axios.get('http://125.6.37.30:4000/api/todo-list');
      console.log('axios get ', response);
    } catch (error) {
      // 
      console.error(error);
    }
  }

  return (
    <div className="appContainer">
      <div className="todoTitle">
        <p>To do List</p>
      </div>
      <div className="todoListContainer">
        <div className="currentDate">
          <Clock/>
        </div>
        <TodoHeader todoCount={todoState.filter(todo => todo.checked === false)}/>
        <div className="todoList">
          <InputForm value={inputState}
                     onKeyPress={handleKeyPress}
                     onChange={handleInputChange}
                     onCreate={handleInputCreate}/>
          <TodoList todos={todoState} onToggle={handleToggle}
                    onRemove={handleInputRemove}/> {/*todos    */}
        </div>
      </div>
    </div>
  );
};

export default App;
